<template>
  <ion-page>
    <ion-content class="login" scroll-y="true">
      <div class="">
        <div class="logo ion-padding"></div>
      </div>
      <div class="top-radius"></div>
      <div class="content-login" style="padding: 20px">
        <br />
        <center>
          <!-- <img src="../assets/lili.png" alt="" /> -->
          <!-- <h5>Pemberdayaan Kemitraan Masyarakat 2024</h5> -->
          <!-- <img
            style="width: 75px; height: 75px"
            src="../views/unib.png"
            alt="ooo"
          />
          <img
            style="width: 75px; height: 75px"
            src="../views/pp.png"
            alt="ooo"
          />

          <img
            style="width: 80px; height: 80px"
            src="../views/tt.png"
            alt="ooo"
          /> -->
          <br />
         <img style="height: 20%;" src="../assets/ccc.png" alt="">
        </center>
        <br>
        <p style="margin: 0">Silakan Login dengan akun Anda</p>
        <p style="margin: 0">Dan Jika Belum Terdaftar, <br>Silahkan Pilih Daftar Terlebih dahulu</p>

        <div class="input-group">
          <div class="label">
            <h2>Username</h2>
            <span>*</span>
          </div>
          <div class="input ion-activatable ripple-parent">
            <ion-icon class="start" :icon="mail" color="primary"></ion-icon>
            <ion-input
              class="username"
              type="text"
              placeholder="Masukkan username Anda"
              v-model="username"
            ></ion-input>
            <ion-ripple-effect mode="ios"></ion-ripple-effect>
          </div>
          <div class="label">
            <h2>Password</h2>
            <span>*</span>
          </div>
          <div class="input ion-activatable ripple-parent">
            <ion-icon
              class="start"
              :icon="lockClosed"
              color="primary"
            ></ion-icon>
            <ion-input
              class="password"
              @keyup.enter="login"
              :type="typePass"
              placeholder="Masukkan password Anda"
              v-model="password"
            ></ion-input>
            <ion-ripple-effect mode="ios"></ion-ripple-effect>
            <ion-icon
              class="end"
              v-if="typePass == 'password'"
              color="primary"
              :icon="eyeOutline"
              @click.prevent="showPass('text')"
            ></ion-icon>
            <ion-icon
              class="end"
              v-else
              color="primary"
              :icon="eyeOffOutline"
              @click.prevent="showPass('password')"
            ></ion-icon>
          </div>
        </div>
        <h5
          class="ion-text-center"
          style="margin: 0; color: var(--ion-color-danger)"
          v-if="errored"
        >
          {{ errMsg }}
        </h5>
        <ion-button
          mode="ios"
          class="login"
          @click.prevent="login"
          expand="block"
        >
          <span style="font-size: 1.5rem; color: white">Masuk</span>
        </ion-button>

        <ion-button
          mode="ios"
          class="login"
          @click.prevent="goToRegisterPage"
          expand="block"
        >
          <span style="font-size: 1.5rem; color: white">Daftar</span>
        </ion-button>
      </div>
      <ion-loading
        mode="ios"
        :is-open="loading"
        message="Mohon tunggu..."
        spinner="crescent"
      ></ion-loading>
      <ion-alert
        :is-open="showAlert"
        header="Pemberitahuan"
        :message="alertMessage"
        buttons="OK"
        @did-dismiss="() => (showAlert.value = false)"
      ></ion-alert>
    </ion-content>
  </ion-page>
</template>

<script setup>
import {
  IonPage,
  IonContent,
  IonIcon,
  IonInput,
  IonRippleEffect,
  IonButton,
  IonLoading,
  IonAlert,
  useBackButton,
} from "@ionic/vue";
import { ref, onMounted } from "vue";
import { mail, lockClosed, eyeOffOutline, eyeOutline } from "ionicons/icons";
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import { useRouter, useRoute } from "vue-router";
import AlertMssg from "../composables/alert";

const loading = ref(false);
const username = ref("");
const password = ref("");
const typePass = ref("password");
const errMsg = ref("");
const errored = ref(false);
const showAlert = ref(false);
const alertMessage = ref("");
const router = useRouter();
const route = useRoute();
const auth = getAuth();

useBackButton(-1, () => {
  if (router.currentRoute.value.name == "register") {
    AlertMssg.confirmExit();
  }
});

onMounted(() => {
  const message = route.query.message;
  if (message === "nokelas") {
    alertMessage.value =
      "Kelas belum diisi. Silakan lengkapi data kelas Anda di halaman pengaturan.";
    showAlert.value = true;
  }
});

const login = async () => {
  errored.value = false;
  if (!username.value) {
    errored.value = true;
    errMsg.value = "Username tidak boleh kosong";
  } else if (!password.value) {
    errored.value = true;
    errMsg.value = "Password tidak boleh kosong";
  } else {
    loading.value = true;
    try {
      await signInWithEmailAndPassword(auth, username.value, password.value);

      loading.value = false;
      errored.value = false;
      router.push({ name: "splash" });
      setTimeout(() => {
        username.value = "";
        password.value = "";
      }, 1000);
    } catch (error) {
      loading.value = false;
      errored.value = true;
      errMsg.value = error.message;
    }
  }
};

const showPass = (type) => {
  typePass.value = type;
};

const goToRegisterPage = () => {
  router.push({ name: "register" }); // Pastikan "register" adalah nama rute yang benar sesuai konfigurasi Vue Router Anda
};
</script>

